import { Component, OnInit } from '@angular/core';
import { StoreService } from '../services/store.service';
import { trigger, transition, style, animate } from '@angular/animations';

@Component({
  selector: 'app-splash-screen',
  templateUrl: './splash-screen.component.html',
  styleUrls: ['./splash-screen.component.css'],
  animations: [
    trigger(
      'inOutAnimation',
      [
        transition(
          ':enter',
          [
            style({ width: 0, height: 0, opacity: 0 }),
            animate('.5s', style({ width: 600, height: 375, opacity: 1 }))
          ]
        ),
        transition(
          ':leave',
          [
            style({ width: 600, height: 375, opacity: 1 }),
            animate('.5s', style({ width: 0, height: 0, opacity: 0 }))
          ]
        )
      ]
    )
  ]
})
export class SplashScreenComponent implements OnInit {
  lose: boolean;
  win: boolean;
  isVisible: boolean;

  constructor(private store: StoreService) { }

  ngOnInit() {
    this.store.gameOver$.subscribe(_ => this.onGameOver(_));
    this.store.win$.subscribe(_ => this.onWin(_));
  }


  onClick() {
    this.lose = false;
    this.win = false;
    this.isVisible = false;

    this.store.nextMatch();
  }

  onGameOver(gameOver: boolean) {
    if (gameOver) {
      this.lose = true;
      this.isVisible = true;
    }
  }

  onWin(win: boolean) {
    if (win) {
      this.win = true;
      this.isVisible = true;
    }
  }
}
