import { Component, OnInit } from '@angular/core';
import { StoreService } from '../services/store.service';

@Component({
  selector: 'app-fail-status',
  templateUrl: './fail-status.component.html',
  styleUrls: ['./fail-status.component.css']
})
export class FailStatusComponent implements OnInit {

  private maximumFails = 9;
  statuses: boolean[] = new Array(this.maximumFails);

  constructor(private store: StoreService) { }

  ngOnInit() {
    this.store.fails$.subscribe(_ => {
      if (_ < this.maximumFails) {
        this.statuses[_ - 1] = true;
      } else if (_ === this.maximumFails) {
        this.statuses[_ - 1] = true;
        this.store.gameOver();
      } else {
        this.store.gameOver();
      }
    });
    this.store.nextMatch$.subscribe(_ => this.onNextMatch(_));
  }

  onNextMatch(nextMatch: boolean) {
    if (nextMatch) {
      this.statuses = new Array(this.maximumFails);
    }
  }
}
