import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class StoreService {
  private _lastLetter$: BehaviorSubject<string> = new BehaviorSubject(null);
  public lastLetter$: Observable<string> = this._lastLetter$.asObservable() as Observable<string>;

  private _fails$: BehaviorSubject<number> = new BehaviorSubject(0);
  public fails$: Observable<number> = this._fails$.asObservable() as Observable<number>;

  private _gameOver$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  public gameOver$: Observable<boolean> = this._gameOver$.asObservable() as Observable<boolean>;

  private _win$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  public win$: Observable<boolean> = this._win$.asObservable() as Observable<boolean>;

  private _nextMatch$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  public nextMatch$: Observable<boolean> = this._nextMatch$.asObservable() as Observable<boolean>;


  constructor() { }

  get lastLetter(): string {
    return this._lastLetter$.getValue();
  }

  set lastLetter(lastLetter: string) {
    this._lastLetter$.next(lastLetter);
  }

  newFailure() {
    this._fails$.next(this._fails$.getValue() + 1);
  }

  gameOver() {
    this._gameOver$.next(true);
  }

  win() {
    this._win$.next(true);
  }

  nextMatch() {
    this._fails$.next(0);
    this._nextMatch$.next(true);
  }
}
