import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Character, GraphQLModel } from '../models/character';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private apiEndpoint = 'https://rickandmortyapi.com/graphql';

  constructor(private httpClient: HttpClient) { }

  getRandom(id: number): Observable<GraphQLModel> {
    return this.httpClient.get<GraphQLModel>(this.buildQuery(id));
  }

  private buildQuery(id: number) {
    return `${this.apiEndpoint}?query=`
      + `{character(id:${id}){name,species,type,gender,origin{name},location{name},image,episode{name,episode,air_date}}}`;
  }
}


