import { Component, OnInit, Input } from '@angular/core';
import { StoreService } from '../services/store.service';

@Component({
  selector: 'app-key',
  templateUrl: './key.component.html',
  styleUrls: ['./key.component.css']
})
export class KeyComponent implements OnInit {
  @Input() letter: string;
  isClickable = true;

  constructor(private store: StoreService) { }

  ngOnInit() {
    this.store.gameOver$.subscribe(_ => this.onGameOver(_));
    this.store.win$.subscribe(_ => this.onWin(_));
    this.store.nextMatch$.subscribe(_ => this.onNextMatch(_));
  }

  onClick() {
    if (this.isClickable) {
      this.store.lastLetter = this.letter;

      this.isClickable = false;
    }
  }

  onGameOver(gameOver: boolean) {
    if (gameOver) {
      this.isClickable = false;
    }
  }

  onWin(win: boolean) {
    if (win) {
      this.isClickable = false;
    }
  }

  onNextMatch(nextMatch: boolean) {
    if (nextMatch) {
      this.isClickable = true;
    }
  }
}
